<template>
  <div class="wrapper">
    <div class="title">我的订单</div>
    <div class="orders">
        <div
         class="order"
         v-for="(item, index) of list"
         :key="index"
        >
            <div class="order__title">
                {{item.shopName}}
                <span
                 :class="{'order__title__status': true, 'order__title__status--success': !item.isCanceled}"
                >{{item.isCanceled ? '已取消' : '已下单'}}</span>
            </div>
            <div class="order__content">
                <div class="order__content__imgs">
                    <template
                     v-for="(innerItem, innerIndex) of item.products"
                     :key="innerIndex"
                    >
                        <img
                         class="order__content__imgs__img"
                         :src="innerItem.product.img"
                         v-if="innerIndex < 4"
                        />
                    </template>
                </div>
                <div class="order__content__info">
                    <div class="order__content__info__price">¥ {{item.totalPrice}}</div>
                    <div class="order__content__info__count">共 {{item.totalNumber}} 件</div>
                </div>
            </div>
        </div>
    </div>
  </div>
  <Docker :currentIndex="2" />
</template>

<script>
import { reactive, toRefs } from 'vue'
import { get } from '../../utils/request'
import Docker from '../../components/Docker.vue'

// 处理订单列表逻辑
const useOrderListEffect = () => {
  const data = reactive({ list: [] })
  const getOrderList = async () => {
    const result = await get('/api/order')
    if (result?.errno === 0 && result?.data?.length) {
      const orderList = result.data
      orderList.forEach((order) => {
        const products = order?.products || []
        let totalPrice = 0
        let totalNumber = 0
        products.forEach((productItem) => {
          totalPrice += ((productItem?.product?.price) * (productItem?.orderSales) || 0)
          totalNumber += (productItem.orderSales || 0)
        })
        order.totalPrice = totalPrice
        order.totalNumber = totalNumber
      })

      data.list = result.data
    }
  }
  getOrderList()
  const { list } = toRefs(data)
  return { list }
}

export default {
  name: 'OrderList',
  components: { Docker },
  setup () {
    const { list } = useOrderListEffect()

    return { list }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/viriables.scss';
.wrapper {
  overflow-y: auto;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: .5rem;
  background: $dark-bgColor;
}
.title {
  line-height: .44rem;
  font-size: .16rem;
  color: $content-fontcolor;
  background: $bgColor;
  text-align: center;
}
.orders {
  padding: 0 .18rem;
  height: 7.1rem;
  overflow: scroll;
}
.order {
  height: 1.1rem;
  margin: .16rem 0;
  background: $bgColor;
  border-radius: .04rem;
  &__title {
    line-height: .22rem;
    padding: .16rem;
    color: $content-fontcolor;
    font-size: .16rem;
    &__status {
      float: right;
      line-height: .2rem;
      font-size: .14rem;
      color: $light-fontColor;
      &--success {
        color: $btn-bgColor;
      }
    }
  }
  &__content {
    padding: 0 .16rem .16rem .16rem;
    display: flex;
    &__imgs {
      flex: 1;
      margin-right: .2rem;
      &__img {
        width: .4rem;
        height: .4rem;
        margin-right: .12rem;
      }
    }
    &__info {
        text-align: right;
      &__price {
          margin-bottom: .04rem;
        line-height: .2rem;
        font-size: .14rem;
        color: $hightLight;
      }
      &__count {
        line-height: .14rem;
        font-size: .12rem;
        color: $content-fontcolor;
      }
    }
  }
}
</style>
